import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-cell-master-list',
  templateUrl: './cell-master-list.component.html',
  styleUrls: ['./cell-master-list.component.css'],
})
export class CellMasterListComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = '/donation/cell_master_add';
    this._component = 'Donation Cell Master';
    this.initTables();
  }
  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = [
      'id',
      'actions',
      'title',
      'bg',
      'le',
      'ou',
      'su',
      'address',
      'location_id',
      'majlis_id',
    ];
    this._tbls[URLz.$].endpoint = URLz.DCO
    if (this._ss.checkPermission(this.ACTION.STATUS)) {
      this._tbls[URLz.$].columns.splice(2, 0, 'activate')
    }
    this._formCreator(this._tbls[URLz.$])
    this._refresh(URLz.$)

  }
}
