import { Component, Injector, OnInit } from '@angular/core';
import { FormControl} from '@angular/forms';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';
import { BasicInfo } from '../../../interface/user/basic-info';
import { ChangeEvent } from 'src/app/interface/common/change-event';
import { PartialSubmit } from 'src/app/interface/common/partial-submit';
import { BaseForm } from 'src/app/class/base.form';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './psdc.component.html',
  styleUrls: ['./psdc.component.css']
})
export class PsdcComponent extends BaseForm implements OnInit {

  constructor(
    injector : Injector,
  ) {
    super(injector);
   }

  ngOnInit(): void {
    this.initForm();
    this.param.endpoint = URLz.PSDC
    this._fhs._pathLocation = '/donation/psdc';
    this.patchData();

  }

  initCtrl(){
    return this._fb.group({
      attribute_id:['',this._vs._vals('Attribute')],
      id:['',this._vs._vals('Attribute Value')]
    })
  }

  initForm(){
    this._fs._form = this._fb.group({
        wajiba: this._fb.group({
          ...this.initCtrl().controls
        }),
        nafila: this._fb.group({
          ...this.initCtrl().controls
        })
    }, { validator: this._vs._matchValidator('attribute_id', 'id','wajiba','nafila') }
    )
  }
//

  patchData() {
    this._http.get({...this.param}).subscribe((res: any) => {
      const data = res.data.records;
      this._fs._form.get('wajiba').patchValue({
        attribute_id : data?.wajiba?.attribute_id,
        id : data?.wajiba?.id
      });
      this._fs._form.get('nafila').patchValue({
        attribute_id : data?.nafila?.attribute_id,
        id : data?.nafila?.id
      });
      this._fs._form.markAllAsTouched();
    });
  }

  _onSubmity(ps: PartialSubmit = this._fss.defaultBehaviour) {
    super._onSubmity({
      body: (ps:PartialSubmit) => {
        ps.param.body = this._fs._form.getRawValue();
      },
      httpResultAction: () => {}
    })
  }

}
