import { Component, Injector, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { BaseForm } from "src/app/class/base.form";
import { URLz } from "src/app/enums/url.enum";
import { DonationType } from "src/app/interface/donation/donation-type";

@Component({
  selector: "app-type-add",
  templateUrl: "./type-add.component.html",
  styleUrls: ["./type-add.component.css"],
})
export class TypeAddComponent extends BaseForm implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
    this.param.endpoint = URLz.DONATION_T
  }

  initform() {
    this._fs._form = this._fb.group({
      // id: [""],
      title: ["", this._vs._val('Donation Type', { minChar: 4, alpha: 1 })],
      code: ["", this._vs._vals('COA Code')],
      sysman_code: ["", this._vs._val('Sysman Account Code', {minChar: 8, maxChar: 14, num:1})],
      category: ["", this._vs._vals('Donation Account Type')],
      description: ["", this._vs._val('', { maxChar: 255, specialChar: 1 }),
      ],
    });
  }
  ngOnInit(): void {
    this._fhs._pathLocation = "/donation/type";
    this._activeId = this._fhs._getURLParam('id');
    this.initform();
    if (this._activeId != "null" && Number(this._activeId) > 0) {
      this.patchData();
    } else {
      this._fs._form.removeControl("id");
    }
  }
  patchData() {
    this._http
      .get({ ...this.param, resource: this._activeId })
      .subscribe((res: any) => {
        let data = res.data.row;
        this._fs._form.patchValue({
          title: data.title,
          code: data.code,
          category: data.category,
          description: data.description,
        });
        this._fs._form.addControl(
          "id", new FormControl(data.id)
        );
      });
  }

  applyClass = { 'col-lg-3': false, 'col-md-4': false, 'col-md-12': true };
}
