import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { ACTION } from 'src/app/enums/action.enum';
import { LB } from 'src/app/class/build/list.builder';
import { BaseForm } from 'src/app/class/base.form';
import { of } from 'rxjs';

@Component({
  selector: 'app-type-list',
  templateUrl: './type-list.component.html',
  styleUrls: ['./type-list.component.css']
})
export class TypeListComponent extends BaseForm implements OnInit {
  constructor(injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._component = "Donation Type"
    this.initTables();
  }
  lb: LB;
  initTables() {
    this.lb = new LB(
      this.injector,
      { endpoint: URLz.DONATION_T },
      ['id', 'actions', 'title', 'sysman_code','code', 'category', 'description']
    )
    this.lb._path = "/donation/type_add"
    this.lb.beforeHit = () => {
      return of(this.lb)
    }
    if (this._ss.checkPermission(ACTION.STATUS)) {
      this.lb.cols.splice(2, 0, 'activate')
    }
    this.lb.init();
    // this.lb.next = (res) => {
    //   this.lb.ds.data = res.data.records;
    //   this.lb.tbl.length = res.data.totalRecords;
    // }
    // this.lb.init()

  }


}
