import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { ACTION } from 'src/app/enums/action.enum';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'app-sub-type-list',
  templateUrl: './sub-type-list.component.html',
  styleUrls: ['./sub-type-list.component.css']
})
export class SubTypeListComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/donation/sub_type_add";
    this._component = "Donation Subtype";
    this.initTables();
  }

  initTables(){
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = ['id', 'actions' ,'title', 'donation_type_id', 'category','description'];
    if (this._ss.checkPermission(ACTION.STATUS)) {
      this._tbls[URLz.$].columns.splice(2, 0, 'activate')
    }
    this._tbls[URLz.$].endpoint = URLz.DONATION_S_T;
    this._formCreator(this._tbls[URLz.$]);
    this._refresh(URLz.$)
  }
}
