import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { CellMaster } from 'src/app/interface/donation/donation-cell-master';
import { BaseForm } from 'src/app/class/base.form';

@Component({
  selector: 'app-cell-master-add',
  templateUrl: './cell-master-add.component.html',
  styleUrls: ['./cell-master-add.component.css'],
})
export class CellMasterAddComponent extends BaseForm implements OnInit {
  constructor(
    injector: Injector,
  ) {
    super(injector);
    this.param.endpoint = URLz.DCO;
  }
  ngOnInit() {
    this._fhs._pathLocation = '/donation/cell_master';
    this.initForm();
    this._activeId = this._fhs._getURLParam('id')
    if (this._activeId != null && Number(this._activeId) > 0) {
      this.patchData();
    }
  }
  initForm() {
    this._fs._form = this._fb.group({
      title: ['', this._vs._val('Cell Master', {minChar: 4, maxChar: 100, alphaNum: 1,specialChar: 1})],
      bg: ['', this._vs._vals('Business Group')],
      le: ['', this._vs._vals('Legal Entity')],
      ou: ['', this._vs._vals('Operating Unit')],
      su: ['', this._vs._vals('Sub Unit')],
      address: ['', this._vs._val('Address', {minChar: 1, maxChar: 100, alphaNum: 1,specialChar: 1})],
      location_id: ['', this._vs._vals('Location 5th Level')],
      majlis_id: ['', this._vs._vals('Majlis')],
    });
  }
  data: any
  patchData() {
    this._http.get({...this.param, resource: this._activeId}).subscribe((res: any) => {
      const data: CellMaster = this.data =  res.data.row;
      this._fs._form.patchValue({
        title: data.title,
        bg: data.bg,
        le: data.le,
        ou: data.ou,
        su: data.su,
        address: data.address,
        location_id: data.location_id,
        majlis_id: data.majlis_id,
      });

    });
  }
}
