import { Component, Injector, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'app-sub-type-add',
  templateUrl: './sub-type-add.component.html',
  styleUrls: ['./sub-type-add.component.css']
})
export class SubTypeAddComponent extends BaseForm implements OnInit {
  constructor(
    injector: Injector,
  ) {
    super(injector);
  }

  public DONATE_CATEGORY= [
    {title:"Wajiba",id:"wajiba"},
    {title:"Nafila",id:"nafila"},
  ]
  ngOnInit(): void {
    this.param.endpoint = URLz.DONATION_S_T;
    this._activeId = this._fhs._getURLParam('id');
    this._fhs._pathLocation = "/donation/sub_type";
    this.initform();
    if (this._activeId != "null" && Number(this._activeId) > 0) {
     this.patchData();
    } else
    this._fs._form.removeControl("id");
  }
  initform() {
    this._fs._form = this._fb.group({
      title: ["", this._vs._val('Title',{minChar: 3,maxChar: 100,alpha: 1})],
      donation_type_id: ["", this._vs._vals('Donation Type ')],
      category: ["",this._vs._vals('Donation Category')],
      description: ["", this._vs._val('', {specialChar: 1})],
    });
  }
  patchData(){
    this._http.get({...this.param, resource: this._activeId}).subscribe((res: any) => {
      let data = res.data.row; // Real API
      this._fs._form.patchValue({
        title: data.title,
        donation_type_id: data.donation_type_id,
        category: data.category,
        description: data.description,
      });
      this._fs._form.addControl(
        "id", new FormControl(res.data.row.id)
      );
    });
  }
}
