import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { CellMasterAddComponent } from './cell-master-add/cell-master-add.component';
import { CellMasterListComponent } from './cell-master-list/cell-master-list.component';
import { DonationMonthlyTargetComponent } from './donation-monthly-target/donation-monthly-target.component';
import { SubTypeAddComponent } from './sub-type-add/sub-type-add.component';
import { SubTypeListComponent } from './sub-type-list/sub-type-list.component';
import { TypeAddComponent } from './type-add/type-add.component';
import { TypeListComponent } from './type-list/type-list.component';
import { PsdcComponent } from './psdc/psdc.component';
import { ACTION } from 'src/app/enums/action.enum';

export const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: '',
        redirectTo: 'cell_master',
        pathMatch: 'full'
      },
      {
        path: "cell_master_add",
        component: CellMasterAddComponent,
        data: {
          title: "Cell Master Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donation"},
            { title: "Cell Master" },
            { title: "Add" },
          ]
        }
      },
      {
        path: "cell_master",
        component: CellMasterListComponent,

        data: {
          title: "Cell Master",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Donation"},
            { title: "Cell Master"},
            { title: "List" }
          ]
        }
      },
      {
        path: "sub_type_add",
        component: SubTypeAddComponent,

        data: {
          title: "Sub Type Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donation"},
            { title: "Sub Type" },
            { title: "Add" }
          ]
        }
      },
      {
        path: "sub_type",
        component: SubTypeListComponent,
        data: {
          title: "Sub Type",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Donation"},
            { title: "Sub Type"},
            { title: "List" },
          ]
        }
      },
      {
        path: "type_add",
        component: TypeAddComponent,

        data: {
          title: "Type Add",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donation"},
            { title: "Type" },
            { title: "Add" }
          ]
        }
      },
      {
        path: "type",
        component: TypeListComponent,
        data: {
          title: "Type",
          permission: [
            ACTION.VIEW
          ],
          urls: [
            { title: "Donation"},
            { title: "Type"},
            { title: "List" },
          ]
        }
      },
      {
        path: "monthly_target",
        component: DonationMonthlyTargetComponent,
        data: {
          title: "Monthly Target",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donation"},
            { title: "Monthly Target" },
            { title: "Add" }
          ]
        }
      },
      {
        path: "psdc",
        component: PsdcComponent,
        data: {
          title: "PSDC",
          permission: [
            ACTION.ADD,
            ACTION.EDIT
          ],
          urls: [
            { title: "Donation"},
            { title: "PSDC" },
            { title: "Add" }
          ]
        }
      },
    ]
  }
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DonationRoutingModule { }
